      >
        <i :class="menu.icon" :style="{ color: menu.color }"></i>
        <span>&emsp; {{ menu.label }}</span>
      </div>
    </main>
  </div>
</template>

<script>
export default {
  data() {
    return {
      menus: [
        { label: 'Operasional', icon: 'fas fa-clipboard-list', route: '/operasional', color: '#4CAF50' }, // Green
        { label: 'PO Material', icon: 'fas fa-cubes', route: '/po-material', color: '#FF9800' }, // Orange
        { label: 'Alat / Asset', icon: 'fas fa-wrench', route: '/alat-asset', color: '#2196F3' }, // Blue
        { label: 'Kasbon', icon: 'fas fa-wallet', route: '/kasbon', color: '#9C27B0' }, // Purple
        { label: 'Profile', icon: 'fas fa-user', route: '/profile', color: '#E91E63' }, // Pink
        { label: 'About', icon: 'fas fa-info-circle', route: '/about', color: '#607D8B' }, // Grey
        { label: 'Setting', icon: 'fas fa-cog', route: '/setting', color: '#FF5722' }, // Deep Orange
      ],
    };
  },
  methods: {
    goTo(route) {
