<template>
    <div class="operasional-form">
        <v-container>
            <v-card class="mx-auto elevation-4" max-width="600" outlined>
                <v-card-title class="text-h5 text-center">Pengajuan Operasional</v-card-title>
                <v-divider></v-divider>

                <v-card-text>
                    <!-- Lokasi -->
                    <div class="section">
                        <v-select v-model="selectedLocation"
                            :items="Object.entries(location).map(([id, name]) => ({ id, name }))" item-value="id"
                            item-text="name" label="Pilih Lokasi" outlined dense class="location-select"></v-select>
                    </div>

                    <!-- Nama Operasional -->
                    <div class="section">
                        <v-btn @click="showOpsPicker = true" color="primary" text-color="blue darken-3"
                            class="mb-3 btn-custom" outlined>
                            <v-icon left>mdi-plus-circle</v-icon> Tambah Ops
                        </v-btn>

                        <!-- Dialog untuk Memilih Ops -->
                        <v-dialog v-model="showOpsPicker" max-width="500" transition="dialog-bottom-transition">
                            <v-card>
                                <v-card-title class="text-h6">Pilih Operasional</v-card-title>
                                <v-divider></v-divider>
                                <v-card-text>
                                    <v-text-field v-model="opsSearch" placeholder="Cari Ops..." solo></v-text-field>
                                    <v-list dense>
                                        <v-list-tile v-for="(ops, index) in filteredOpsList" :key="index"
                                            @click="handleOpsChange(ops.name)" class="result-item">
                                            <v-list-tile-content>
                                                <v-list-tile-title>{{ ops.name }}</v-list-tile-title>
                                            </v-list-tile-content>
                                        </v-list-tile>
                                        <v-list-tile v-if="filteredOpsList.length === 0">
                                            <v-list-tile-content>
                                                <v-list-tile-title>Ops tidak ditemukan</v-list-tile-title>
                                            </v-list-tile-content>
                                        </v-list-tile>
                                    </v-list>
                                </v-card-text>
                                <v-divider></v-divider>
                                <v-card-actions>
                                    <v-spacer></v-spacer>
                                    <v-btn text @click="showOpsPicker = false">Tutup</v-btn>
                                </v-card-actions>
                            </v-card>
                        </v-dialog>

                        <!-- Daftar Ops yang Dipilih -->
                        <v-layout row wrap v-if="selectedOpsList.length > 0" class="selected-ops-list">
                            <v-flex xs12 v-for="(ops, index) in selectedOpsList" :key="index" class="selected-ops-item">
                                <v-card elevation="2" class="pa-3 mb-2">
                                    <p class="font-weight-bold">{{ ops.name }}</p>
                                    <v-text-field v-if="ops.ops_master_budget?.price > 0"
                                        :value="ops.ops_master_budget.price" label="Nominal" readonly dense
                                        outlined></v-text-field>
                                    <v-text-field v-else v-model="ops.nominal" label="Nominal" type="number" dense
                                        outlined @input="handleNominalChange(index, $event)"></v-text-field>
                                    <v-text-field v-model="ops.qty" label="Qty" type="number" dense outlined
                                        @input="handleQtyChange(index, $event)"></v-text-field>
                                    <p class="text-body-2 font-weight-light total">Total: {{ ops.total }}</p>
                                    <v-btn icon small @click="handleRemoveOps(index)">
                                        <v-icon color="red">mdi-delete</v-icon>
                                    </v-btn>
                                </v-card>
                            </v-flex>
                        </v-layout>
                    </div>

                    <!-- Nama Toko -->
                    <div class="section">
                        <v-btn @click="showStorePicker = true" color="primary" outlined class="mb-3 btn-custom">
                            <v-icon left>mdi-plus-circle</v-icon> Toko
                        </v-btn>

                        <v-dialog v-model="showStorePicker" max-width="500">
                            <v-card>
                                <v-card-title class="text-h6">Pilih Toko</v-card-title>
                                <v-divider></v-divider>
                                <v-card-text>
                                    <v-text-field v-model="storeSearch" placeholder="Cari Toko..." solo></v-text-field>
                                    <v-list dense>
                                        <v-list-tile v-for="(store, index) in filteredStoreList" :key="index"
                                            @click="handleStoreChange(store.name)" class="result-item">
                                            <v-list-tile-content>
                                                <v-list-tile-title>{{ store.name }}</v-list-tile-title>
                                            </v-list-tile-content>
                                        </v-list-tile>
                                        <v-list-tile v-if="filteredStoreList.length === 0">
                                            <v-list-tile-content>
                                                <v-list-tile-title>Toko tidak ditemukan</v-list-tile-title>
                                            </v-list-tile-content>
                                        </v-list-tile>
                                    </v-list>
                                </v-card-text>
                                <v-divider></v-divider>
                                <v-card-actions>
                                    <v-spacer></v-spacer>
                                    <v-btn text @click="showStorePicker = false">Tutup</v-btn>
                                </v-card-actions>
                            </v-card>
                        </v-dialog>

                        <v-layout row wrap v-if="selectedStoreList.length > 0" class="selected-store-list">
                            <v-flex xs12 v-for="(store, index) in selectedStoreList" :key="index">
                                <v-chip close @click:close="handleRemoveStore(index)" color="indigo lighten-2"
                                    text-color="white">
                                    {{ store.name }}
                                </v-chip>
                            </v-flex>
                        </v-layout>
                    </div>

                    <!-- Section PO -->
                    <div class="section">
                        <v-btn @click="showPoPicker = true" color="primary" outlined class="mb-3 btn-custom">
                            <v-icon left>mdi-plus-circle</v-icon> DO (PO)
                        </v-btn>

                        <v-dialog v-model="showPoPicker" max-width="500">
                            <v-card>
                                <v-card-title class="text-h6">Pilih DO (PO)</v-card-title>
                                <v-divider></v-divider>
                                <v-card-text>
                                    <v-text-field v-model="poSearch" placeholder="Cari DO..." solo></v-text-field>
                                    <v-list dense>
                                        <v-list-tile v-for="(po, index) in filteredPoList" :key="index"
                                            @click="handlePoChange(po.name)" class="result-item">
                                            <v-list-tile-content>
                                                <v-list-tile-title>{{ po.name }}</v-list-tile-title>
                                            </v-list-tile-content>
                                        </v-list-tile>
                                        <v-list-tile v-if="filteredPoList.length === 0">
                                            <v-list-tile-content>
                                                <v-list-tile-title>DO tidak ditemukan</v-list-tile-title>
                                            </v-list-tile-content>
                                        </v-list-tile>
                                    </v-list>
                                </v-card-text>
                                <v-divider></v-divider>
                                <v-card-actions>
                                    <v-spacer></v-spacer>
                                    <v-btn text @click="showPoPicker = false">Tutup</v-btn>
                                </v-card-actions>
                            </v-card>
                        </v-dialog>

                        <v-layout row wrap v-if="selectedPoList.length > 0" class="selected-po-list">
                            <v-flex xs12 v-for="(po, index) in selectedPoList" :key="index">
                                <v-chip close @click:close="handleRemovePo(index)" color="indigo lighten-2"
                                    text-color="white">
                                    {{ po.name }}
                                </v-chip>
                            </v-flex>
                        </v-layout>
                    </div>

                    <!-- Section Complain -->
                    <div class="section">
                        <v-btn @click="showComplainPicker = true" color="primary" outlined class="mb-3 btn-custom">
                            <v-icon left>mdi-plus-circle</v-icon> Complain
                        </v-btn>

                        <v-dialog v-model="showComplainPicker" max-width="500">
                            <v-card>
                                <v-card-title class="text-h6">Pilih Complain</v-card-title>
                                <v-divider></v-divider>
                                <v-card-text>
                                    <v-text-field v-model="complainSearch" placeholder="Cari Complain..."
                                        solo></v-text-field>
                                    <v-list dense>
                                        <v-list-tile v-for="(complain, index) in filteredComplainList" :key="index"
                                            @click="handleComplainChange(complain.name)" class="result-item">
                                            <v-list-tile-content>
                                                <v-list-tile-title>{{ complain.name }}</v-list-tile-title>
                                            </v-list-tile-content>
                                        </v-list-tile>
                                        <v-list-tile v-if="filteredComplainList.length === 0">
                                            <v-list-tile-content>
                                                <v-list-tile-title>Complain tidak ditemukan</v-list-tile-title>
                                            </v-list-tile-content>
                                        </v-list-tile>
                                    </v-list>
                                </v-card-text>
                                <v-divider></v-divider>
                                <v-card-actions>
                                    <v-spacer></v-spacer>
                                    <v-btn text @click="showComplainPicker = false">Tutup</v-btn>
                                </v-card-actions>
                            </v-card>
                        </v-dialog>

                        <v-layout row wrap v-if="selectedComplainList.length > 0" class="selected-complain-list">
                            <v-flex xs12 v-for="(complain, index) in selectedComplainList" :key="index">
                                <v-chip close @click:close="handleRemoveComplain(index)" color="indigo lighten-2"
                                    text-color="white">
                                    {{ complain.name }}
                                </v-chip>
                            </v-flex>
                        </v-layout>
                    </div>



                    <!-- Note -->
                    <v-textarea v-model="note" rows="3" label="Catatan" outlined dense></v-textarea>


                    <!-- Button to trigger file input -->
                    <v-btn color="primary" @click="triggerFileInput" outlined class="mb-3 btn-custom">
                        <v-icon left>mdi-attachment</v-icon> Lampirkan Foto
                    </v-btn>

                    <!-- Hidden file input element -->
                    <input ref="fileInput" type="file" accept="image/*" multiple style="display: none"
                        @change="handleFileUpload" />

                    <!-- Preview of selected and compressed images -->
                    <v-layout v-if="previewImages.length > 0" class="files-container mt-2">
                        <v-flex v-for="(file, index) in previewImages" :key="index" cols="4" class="file-item">
                            <v-img :src="file.data" class="file-thumbnail" width="80" height="80"
                                @click="openFullScreen(file, index)" />
                            <v-btn icon small @click="handleRemoveFile(index)">
                                <v-icon color="red">mdi-delete</v-icon>
                            </v-btn>
                        </v-flex>
                    </v-layout>

                    <!-- Fullscreen image preview dialog -->
                    <v-dialog v-model="isFullScreen" max-width="600">
                        <v-card>
                            <v-img :src="currentImage?.data" max-height="500" />
                            <v-card-actions>
                                <v-spacer></v-spacer>
                                <v-btn icon small @click="handleRemoveFile(currentIndex)">
                                    <v-icon color="red">mdi-delete</v-icon>
                                </v-btn>
                                <v-btn text @click="isFullScreen = false">Tutup</v-btn>
                            </v-card-actions>
                        </v-card>
                    </v-dialog>



                    <!-- Ajukan Button -->
                    <v-btn @click="handleSubmit" color="success" block class="mt-4 btn-submit"
                        style="background-color: blue;color: aliceblue;" large>Ajukan</v-btn>
                    <p v-if="error" class="error-text mt-2">{{ error }}</p>
                </v-card-text>
            </v-card>
        </v-container>
        <v-container v-if="loading" class="d-flex justify-center align-center">
            <v-progress-circular indeterminate color="primary" size="70"></v-progress-circular>
        </v-container>
    </div>
</template>

<script>
import axios from "@/utils/axios";


export default {
    props: ["paramsFromServer"],
    data() {
        return {
            opsList: [],
            selectedOpsList: [],
            showOpsPicker: false,
            storeList: [],
            selectedStoreList: [],
            showStorePicker: false,
            poList: [],
            selectedPoList: [],
            showPoPicker: false,
            complainList: [],
            selectedComplainList: [],
            showComplainPicker: false,
            location: {},
            selectedLocation: "",
            note: "",
            files: [],
            error: "",
            opsSearch: "",
            storeSearch: "",
            poSearch: "",
            complainSearch: "",
            isCompressing: false,
            previewImages: [],
            isFullScreen: false,
            currentImage: null,
            currentIndex: null,
            loading: false,
        };
    },
    computed: {
        filteredOpsList() {
            return this.opsList.filter((ops) =>
                ops?.name?.toLowerCase()?.includes(this.opsSearch.toLowerCase())
            );
        },
        filteredStoreList() {
            return this.storeList.filter((store) =>
                store?.name?.toLowerCase()?.includes(this.storeSearch.toLowerCase())
            );
        },
        filteredPoList() {
            return this.poList.filter((po) =>
                po.name.toLowerCase().includes(this.poSearch.toLowerCase())
            );
        },
        filteredComplainList() {
            return this.complainList.filter((complain) =>
                complain.name.toLowerCase().includes(this.complainSearch.toLowerCase())
            );
        }
    },
    methods: {
        triggerFileInput() {
            this.$refs.fileInput.click();
        },
        async fetchFormOperasional() {
            try {
                const response = await axios.get("/operasional/getOpsFormData");
                this.paramsFromServer = response.data;

            } catch (error) {
                console.error("Error fetching form operasional", error);
            }
        },
        handleLocationChange(itemValue) {
            this.selectedLocation = itemValue;
        },
        handleOpsChange(itemValue) {
            if (!this.selectedOpsList.some((item) => item.name === itemValue)) {
                const selectedOpsObject = this.opsList.find(
                    (ops) => ops.name === itemValue
                );
                // Pastikan nominal selalu ada (misalnya 0 jika tidak ada)
                const nominalValue = selectedOpsObject?.ops_master_budget?.price || 0;

                this.selectedOpsList.push({
                    ...selectedOpsObject,
                    qty: 1,
                    nominal: nominalValue,
                    total: new Intl.NumberFormat("id-ID", {
                        style: "currency",
                        currency: "IDR",
                        minimumFractionDigits: 0,
                    }).format(nominalValue),
                });
            }
            this.showOpsPicker = false;
            this.opsSearch = "";
        },
        handleStoreChange(itemValue) {
            // Pastikan `itemValue` tidak null dan tidak ada item dengan nama yang sama di `selectedStoreList`
            if (itemValue && !this.selectedStoreList.some(item => item.name === itemValue)) {
                const selectedStoreObject = this.storeList.find(store => store.name === itemValue);
                if (selectedStoreObject) {
                    // Tambahkan item yang dipilih ke daftar `selectedStoreList`
                    this.selectedStoreList.push({ ...selectedStoreObject });
                }
            }
            // Tutup picker dan kosongkan pencarian
            this.showStorePicker = false;
            this.storeSearch = '';
        },
        handleQtyChange(index, value) {
            const maxQty = 10;
            if (value > maxQty) {
                this.selectedOpsList[index].qty = maxQty;
                alert("Qty tidak boleh melebihi 10");
                return;
            } else {
                this.selectedOpsList[index].qty = parseInt(value) || 0;
            }
            
            
            const total =
                this.selectedOpsList[index].qty * this.selectedOpsList[index].nominal;
            this.selectedOpsList[index].total = new Intl.NumberFormat("id-ID", {
                style: "currency",
                currency: "IDR",
                minimumFractionDigits: 0,
            }).format(total);
        },
        handleNominalChange(index, value) {
            this.selectedOpsList[index].nominal = parseFloat(value) || 0;
            const total =
                this.selectedOpsList[index].qty * this.selectedOpsList[index].nominal;
            this.selectedOpsList[index].total = new Intl.NumberFormat("id-ID", {
                style: "currency",
                currency: "IDR",
                minimumFractionDigits: 0,
            }).format(total);
        },
        handlePoChange(itemValue) {
            if (!this.selectedPoList.some((item) => item.name === itemValue)) {
                const selectedPoObject = this.poList.find(po => po.name === itemValue);
                this.selectedPoList.push(selectedPoObject);
            }
            this.showPoPicker = false;
            this.poSearch = "";
        },
        handleRemovePo(index) {
            this.selectedPoList.splice(index, 1);
        },
        handleComplainChange(itemValue) {
            if (!this.selectedComplainList.some((item) => item.name === itemValue)) {
                const selectedComplainObject = this.complainList.find(complain => complain.name === itemValue);
                this.selectedComplainList.push(selectedComplainObject);
            }
            this.showComplainPicker = false;
            this.complainSearch = "";
        },
        handleRemoveComplain(index) {
            this.selectedComplainList.splice(index, 1);
        },
        formatFileSize(size) {
            const i = Math.floor(Math.log(size) / Math.log(1024));
            return (size / Math.pow(1024, i)).toFixed(0) * 1 + ' ' + ['B', 'kB', 'MB', 'GB', 'TB'][i];
        },
        async handleFileUpload(event) {
            const files = Array.from(event.target.files);
            this.isCompressing = true;

            try {
                // Kompres semua gambar yang dipilih
                const compressedImages = await Promise.all(files.map(file => this.readAndCompressImage(file)));
                this.previewImages.push(...compressedImages); // Tampilkan pratinjau
            } finally {
                this.isCompressing = false;
            }
        },
        readAndCompressImage(file) {
            return new Promise((resolve, reject) => {
                if (!file.type.startsWith('image/')) {
                    this.$toast.error('Hanya file gambar yang diperbolehkan.');
                    return reject(new Error('Invalid file type'));
                }

                const reader = new FileReader();
                reader.onload = async (e) => {
                    try {
                        const compressedImage = await this.compressImage(e.target.result, file.name);
                        resolve(compressedImage);
                    } catch (error) {
                        reject(error);
                    }
                };
                reader.onerror = error => reject(error);
                reader.readAsDataURL(file);
            });
        },
        async compressImage(dataUrl, fileName) {
            const img = new Image();
            img.src = dataUrl;

            return new Promise((resolve, reject) => {
                img.onload = () => {
                    const canvas = document.createElement('canvas');
                    const ctx = canvas.getContext('2d');
                    const maxDimension = 800;
                    let width = img.width;
                    let height = img.height;

                    // Atur dimensi gambar berdasarkan batas maksimum
                    if (width > height) {
                        if (width > maxDimension) {
                            height = Math.round((height *= maxDimension / width));
                            width = maxDimension;
                        }
                    } else if (height > maxDimension) {
                        width = Math.round((width *= maxDimension / height));
                        height = maxDimension;
                    }

                    canvas.width = width;
                    canvas.height = height;
                    ctx.drawImage(img, 0, 0, width, height);

                    // Proses kompresi
                    let quality = 0.8;
                    let compressedDataUrl;

                    do {
                        compressedDataUrl = canvas.toDataURL('image/jpeg', quality);
                        const byteString = atob(compressedDataUrl.split(',')[1]);
                        const buffer = new ArrayBuffer(byteString.length);
                        const view = new Uint8Array(buffer);
                        for (let i = 0; i < byteString.length; i++) {
                            view[i] = byteString.charCodeAt(i);
                        }
                        const blob = new Blob([buffer], { type: 'image/jpeg' });

                        if (blob.size <= 200 * 1024) {
                            resolve({ name: fileName, data: compressedDataUrl, file: blob, filemtime: Date.now(), size: blob.size });
                            return;
                        }

                        quality -= 0.1;
                    } while (quality > 0);

                    reject('Failed to compress image below 200KB');
                };

                img.onerror = (error) => reject(error);
            });
        },
        openFullScreen(file, index) {
            this.currentImage = file;
            this.currentIndex = index;
            this.isFullScreen = true;
        },
        handleRemoveFile(index) {
            this.previewImages.splice(index, 1); // Menghapus gambar dari pratinjau
        },
        handleRemoveOps(index) {
            this.selectedOpsList.splice(index, 1);
        },
        handleRemoveStore(index) {
            this.selectedStoreList.splice(index, 1);
        },
        async handleSubmit() {
            if (this.selectedOpsList.length === 0) {
                this.error = 'Harap tambahkan minimal satu nama Ops';
                return;
            }
            if (this.selectedOpsList.some(v => v.nominal < 1000)) {
                this.error = 'Nominal tidak boleh kurang dari 1000';
                return;
            }
            const finalTotal = this.selectedOpsList.reduce((acc, item) => acc + (item.qty * item.nominal), 0);
            this.error = '';

            const formData = new FormData();
            formData.append('location_id', this.selectedLocation);
            formData.append('ops', JSON.stringify(this.selectedOpsList));
            formData.append('stores', this.selectedStoreList.map(v => v.id));
            formData.append('pos', this.selectedPoList.map(v => v.id));
            formData.append('complains', this.selectedComplainList.map(v => v.id));
            formData.append('final_total', finalTotal);
            formData.append('note', this.note);

            this.previewImages.forEach((Obj, index) => {
                formData.append(`files[${index}]`, Obj.file, Obj.name);
            });

            

            try {
                this.loading = true;
                const { data } = await axios.post('/operasional/post-ops-form-data', formData, {
                    headers: { 'Content-Type': 'multipart/form-data' },
                });
                if (data?.success) {
                    this.$toast?.success(data?.msg || 'Data berhasil ditambahkan')
                    setTimeout(() => {
                        this.$router.push({
                            name: 'Operasional',
                        });
                    }, 2e3);
                } else {
                    this.$toast?.error(data?.msg || 'Ada kesalahan cek, console.warn')
                    console.log(data)
                }
            } catch (error) {
                console.error(error);
            } finally {
                this.loading = false;
            }
        },
        async fetchParamsFromServer() {
            try {
                const response = await axios.get('/operasional/getOpsFormData');
                this.initializeData(response.data);
            } catch (error) {
                console.error("Gagal mengambil data dari server:", error);
            }
        },
        initializeData(data) {
            // Assign nilai `data` ke variabel lokal di komponen
            this.opsList = data.opsNames || [];
            this.storeList = data.stores || [];
            this.poList = data.poList || [];
            this.complainList = data.complains || [];
            this.location = data.locations || {};
            this.selectedLocation = Object.keys(data.locations || [])[0];
        },
    },

    created() {
        if (this?.$route?.params?.paramsFromServer) {
            this.paramsFromServer = this?.$route?.params?.paramsFromServer
        }
    },

    async mounted() {
        if (!this.paramsFromServer) {
            this.fetchParamsFromServer();
        } else {
            this.initializeData(this.paramsFromServer);
        }


    }
};
</script>

<style scoped>
.operasional-form {
    max-width: 800px;
    margin: auto;
}

.section {
    margin-bottom: 20px;
}

.result-item:hover {
    background-color: #f0f0f0;
    cursor: pointer;
}

.selected-ops-list,
.selected-store-list {
    margin-top: 15px;
}

/* Mengatur gaya untuk tombol outlined */
.btn-custom {
    background-color: white !important;
    border: 1px solid #1976d2 !important;
    /* Warna outline */
    color: #1976d2 !important;
    /* Warna teks dan ikon */
    transition: background-color 0.3s ease, color 0.3s ease;
}

.btn-custom:hover {
    background-color: #1976d2 !important;
    /* Warna latar belakang saat hover */
    color: white !important;
    /* Warna teks dan ikon saat hover */
}

.result-item {
    display: flex;
    align-items: center;
    padding: 8px;
    margin-bottom: 4px;
    border-bottom: 1px solid #e0e0e0;
    /* Membuat garis pemisah */
}

.result-item:last-child {
    border-bottom: none;
    /* Menghilangkan garis pada item terakhir */
}


.total {
    font-weight: bold;
    color: #4caf50;
}

.btn-submit {
    font-weight: bold;
    font-size: 18px;
}

.files-container {
    display: flex;
    flex-wrap: wrap;
}

.file-item {
    position: relative;
}

.file-thumbnail {
    width: 80px;
    height: 80px;
    object-fit: cover;
}

.file-image {
    border-radius: 8px;
    height: 80px;
    object-fit: cover;
    border: 2px solid #eee;
