<template>
    <div class="absensi-slider">
      <div class="slider-container">
        <label class="slider-label">Check Lock In / Check Lock Out</label>
        <input
          type="range"
          v-model="sliderValue"
          min="0"
          max="1"
          step="1"
          class="slider"
          @input="handleSlider"
        />
        <div class="slider-status">
          {{ sliderStatus }}
        </div>
      </div>
      
    </div>
  </template>
  
  <script>
  import axios from '@/utils/axios';
//   import { getLocation } from '@/utils/geo';
  import Vue from 'vue';
  
  export default {
    props: {
      location: {
        type: Object,
        required: true,
      },
    },
    data() {
      return {
        sliderValue: 0, // 0 untuk "Check Lock In", 1 untuk "Check Lock Out"
        locationDetails: null,
        errorMsg: null,
      };
    },
    computed: {
      sliderStatus() {
        return this.sliderValue === 0 ? 'Check Lock In' : 'Check Lock Out';
      },
    },
    methods: {
      handleSlider() {
        if (this.sliderValue === 0) {
          this.handleCheckLockIn();
        } else if (this.sliderValue === 1) {
          this.handleCheckLockOut();
        }
      },
      async handleCheckLockIn() {
        try {
          await axios.post('/check-lock', {
            latitude: this.location.latitude,
            longitude: this.location.longitude,
            description: 'Check Lock In',
            name: 'check_clock_in',
          });
          Vue.$toast.success('Check Lock In successful');
        } catch (error) {
          Vue.$toast.error(error.message);
          console.error(error);
        }
      },
      async handleCheckLockOut() {
        try {
          await axios.post('/check-lock', {
            latitude: this.location.latitude,
            longitude: this.location.longitude,
            description: 'Check Lock Out',
            name: 'check_clock_out',
          });
          Vue.$toast.success('Check Lock Out successful');
        } catch (error) {
          Vue.$toast.error(error.message);
          console.error(error);
        }
      },
    },
    // mounted() {
    //   this.fetchLocation();
    // },
  };
  </script>
  
  <style scoped>
  .absensi-slider {
    margin: 20px;
    text-align: center;
  }
  
  .slider-container {
    margin-bottom: 20px;
  }
  
  .slider-label {
    font-size: 18px;
    margin-bottom: 10px;
  }
  
  .slider {
    width: 100%;
  }
  
  .slider-status {
    margin-top: 10px;
    font-size: 16px;
    font-weight: bold;
  }
  
  .location-text {
    margin-top: 20px;
    font-size: 14px;
    color: #555;
  }
  </style>
  