<template>
  <div class="home-container" v-if="user">
    <!-- Welcome Message -->
    <div class="welcome-message">
      Welcome, {{ user.surname }} {{ user.first_name }} {{ user.last_name }}
    </div>

    <!-- WebView Section (Simulating the WebView behavior) -->
    <div v-if="statistic.HtmlInfo" class="webview-container" :style="{ height: webViewHeight + 'px' }">
      <iframe
        class="webview"
        :srcdoc="statistic.HtmlInfo"
        @load="handleIframeLoad"
        ref="iframe"
      ></iframe>
    </div>

    <!-- Refresh Button (Simulating Pull to Refresh) -->
    <button class="refresh-button" @click="onRefresh">
      Refresh
    </button>

    <!-- AbsensiSlider Component Placeholder -->
    <AbsensiSlider :location="location" />


    <!-- Location Text -->
    <div class="location-text">{{ locationText }}</div>
  </div>
</template>


<script>
import axios from '@/utils/axios';
import { mapState } from 'vuex';
import platform from 'platform';
import { getLocation } from '@/utils/geo';
import Vue from 'vue';
import AbsensiSlider from '@/components/AbsensiSlider'; // Import your AbsensiSlider component

export default {
  components: {
    AbsensiSlider, // Register AbsensiSlider
  },
  data() {
    return {
      loading: true,
      errorMsg: null,
      position: null,
      locationDetails: null,
      deviceName: '',
      refreshing: false,
      webViewHeight: 0, // State for WebView height
    };
  },
  computed: {
    ...mapState(['user', 'statistic']),
    locationText() {
      return this.errorMsg ? this.errorMsg : this.locationDetails ? this.locationDetails.display_name : 'Waiting..';
    },
    location() {
      console.log(this.position, this.locationDetails, 'horee')
      // Gabungkan position dan locationDetails untuk diteruskan ke AbsensiSlider
      return this.position && this.locationDetails
        ? {
            latitude: this.position.coords.latitude,
            longitude: this.position.coords.longitude,
            display_name: this.locationDetails.display_name
          }
        : {};
    }
  },
  methods: {
    async fetchLocation() {
      try {
        const { position, loc } = await getLocation({ showLoc: true });
        this.position = position;
        this.locationDetails = loc;
      } catch (error) {
        console.error('Failed to fetch location:', error);
        this.errorMsg = error.message;
      }
    },
    async handleCheckLockIn() {
      try {
        await axios.post('/check-lock', {
          latitude: this.position.coords.latitude,
          longitude: this.position.coords.longitude,
          description: 'Check Lock In',
          name: 'check_clock_in'
        });
        alert('Success', 'Check Lock In successful');
      } catch (error) {
        Vue.$toast.error(error.message);
        console.error(error);
      }
    },
    async handleCheckLockOut() {
      try {
        await axios.post('/check-lock', {
          latitude: this.position.coords.latitude,
          longitude: this.position.coords.longitude,
          description: 'Check Lock Out',
          name: 'check_clock_out'
        });
        alert('Success', 'Check Lock Out successful');
      } catch (error) {
        Vue.$toast.error(error.message);
        console.error(error);
      }
    },
    async onRefresh() {
      this.refreshing = true;
      // Simulate fetching new data
      await this.fetchLocation();
      this.refreshing = false;
    },
    handleIframeLoad() {
      const iframe = this.$refs.iframe;
      this.webViewHeight = iframe.contentWindow.document.body.scrollHeight;
    }
  },
  mounted() {
    this.deviceName = platform.name + ' ' + platform.version;
    this.fetchLocation();
  }
};
</script>


<style scoped>
.home-container {
  padding: 20px;
  font-family: Arial, sans-serif;
}

.welcome-message {
  padding-top: 10px;
  padding-left: 10px;
  margin-bottom: 15px;
  font-size: 18px;
}

.webview-container {
  width: 100%;
  margin-top: 20px;
  overflow: hidden;
}

.webview {
  width: 100%;
  border: none;
}

.button-container {
  display: flex;
  justify-content: space-around;
  margin-top: 50px;
  margin-bottom: 50px;
}

.button {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  font-size: 17px;
  color: #fff;
  cursor: pointer;
}

.check-in-button {
  background-color: lightgreen;
}

.check-out-button {
  background-color: lightcoral;
}

.refresh-button {
  margin: 20px 0;
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.location-text {
  position: fixed;
  bottom: 10px;
  left: 20px;
  right: 20px;
  text-align: center;
  background-color: rgba(255, 255, 255, 0.7);
  padding: 10px;
  border-radius: 10px;
}
</style>
