<template>
    <v-container>
        <v-card class="mx-auto elevation-2" max-width="600">
            <v-card-title class="headline grey--text text--darken-2">
                Detail Pengajuan Operasional
            </v-card-title>

            <v-divider></v-divider>

            <v-card-text>
                <v-layout row wrap>
                    <v-flex xs12>
                        <strong>No Ref:</strong>
                        <p>{{ operasional.no_ref }}</p>
                    </v-flex>

                    <!-- Location -->
                    <v-flex xs12>
                        <strong>Location:</strong>
                        <p>{{ operasional?.location?.name || 'N/A' }}</p>
                    </v-flex>

                    <!-- Ops List -->
                    <v-flex xs12>
                        <strong>Operasional:</strong>
                        <v-card v-for="(ops, index) in operasional.lines" :key="index" class="ma-2 pa-2" outlined>
                            <p><strong>Nama:</strong> {{ ops.name?.name }}</p>
                            <p><strong>Qty:</strong> {{ formatQuantity(ops.qty) }}</p>
                            <p><strong>Nominal:</strong> {{ formatNumber(ops.nominal) }}</p>
                            <p><strong>Total:</strong> {{ formatNumber(ops.total_amount) }}</p>
                        </v-card>
                    </v-flex>

                    <v-flex xs12>
                        <strong>Total Ops:</strong>
                        <p>{{ formatNumber(operasional?.final_total) }}</p>
                    </v-flex>

                    <!-- Store List -->
                    <v-flex v-if="operasional.for_store?.length" xs12>
                        <strong>Stores:</strong>
                        <v-chip v-for="(store, index) in operasional.for_store" :key="index" class="ma-1" small>
                            {{ store.store_code }} - {{ store.store_name }}
                        </v-chip>
                    </v-flex>

                    <!-- PO List -->
                    <v-flex v-if="operasional.for_po?.length" xs12>
                        <strong>PO (Delivery Order):</strong>
                        <v-chip v-for="(po, index) in operasional.for_po" :key="index" class="ma-1" small>
                            {{ po.trx_no }}
                        </v-chip>
                    </v-flex>

                    <!-- Complain List -->
                    <v-flex v-if="operasional.for_complain?.length" xs12>
                        <strong>Complains:</strong>
                        <v-chip v-for="(complain, index) in operasional.for_complain" :key="index" class="ma-1" small>
                            {{ complain.trx_no }}
                        </v-chip>
                    </v-flex>

                    <!-- Note -->
                    <v-flex v-if="operasional.note" xs12>
                        <strong>Note:</strong>
                        <p>{{ operasional.note }}</p>
                    </v-flex>

                    <!-- Image Attachments -->
                    <v-flex xs12>
                        <strong>Lampiran:</strong>
                        <v-layout row wrap>
                            <v-flex xs4 class="d-flex flex-column align-center">
                                <v-btn @click="handleSelectImage" outlined small class="btn-custom">
                                    <v-icon left>mdi-camera</v-icon>
                                </v-btn>
                            </v-flex>

                            <v-flex v-for="(image, index) in operasional.documents" :key="`existing-${index}`" xs4
                                class="d-flex flex-column align-center">
                                <v-img :src="image.uri" max-width="80" max-height="80"
                                    @click="handleImageClick(image.uri)" class="preview-thumbnail"></v-img>
                            </v-flex>

                            <v-flex v-for="(image, index) in selectedImages" :key="`new-${index}`" xs4
                                class="d-flex flex-column align-center">
                                <v-img :src="image.uri" max-width="80" max-height="80"
                                    @click="handleImageClick(image.uri)" class="preview-thumbnail"></v-img>
                            </v-flex>
                        </v-layout>
                    </v-flex>
                </v-layout>
            </v-card-text>

            <!-- Close Button -->
            <v-card-actions>
                <v-btn block color="red" text @click="onClose">Tutup</v-btn>
            </v-card-actions>

            <!-- Fullscreen Image Preview Modal -->
            <v-dialog v-model="previewImage" max-width="500" persistent v-if="previewImage">
                <v-card>
                    <v-card-title class="text-h5">
                        <span>Lampiran Preview</span>
                        <v-spacer></v-spacer>
                        <v-btn icon @click="previewImage = null">
                            <v-icon>mdi-close</v-icon>
                        </v-btn>
                    </v-card-title>
                    <v-card-text>
                        <v-img :src="previewImage" max-height="500" max-width="100%" />
                    </v-card-text>
                </v-card>
            </v-dialog>
        </v-card>
    </v-container>
</template>

<script>
import axios from "@/utils/axios";
import Vue, { ref } from "vue";

export default {
    props: ["operasional", "onSubmitCompleted"],
    setup(props, { emit }) {
        const selectedImages = ref([]);
        const previewImage = ref(null);

        const onClose = () => {
            emit("close"); // Emit 'close' to the parent
        };


        const formatNumber = (number) => {
            return number % 1 === 0 ? formatCurrency(number) : number.toLocaleString();
        };

        const formatCurrency = (amount) => {
            return parseFloat(amount).toLocaleString("id-ID", {
                style: "currency",
                currency: "IDR",
                minimumFractionDigits: 0,
            });
        };

        const formatQuantity = (qty) => {
            qty = parseFloat(qty) || 0;
            return qty % 1 === 0 ? qty.toFixed(0) : qty.toFixed(2);
        };

        const handleSelectImage = () => {
            const input = document.createElement("input");
            input.type = "file";
            input.accept = "image/*";
            input.onchange = async (event) => {
                const file = event.target.files[0];
                if (file) {
                    await uploadImage(file);
                }
            };
            input.click();
        };

        const uploadImage = async (file) => {
            const formData = new FormData();
            formData.append("photo", file);
            formData.append("ops_id", props.operasional.id);

            try {
                const response = await axios.post("/operasional/upload-photos", formData, {
                    headers: {
                        "Content-Type": "multipart/form-data",
                    },
                });
                const { success, msg, id } = response.data || {};
                if (success) {
                    Vue.$toast?.success(msg || "Data berhasil disimpan.");
                    const uri = URL.createObjectURL(file) 
                    // selectedImages.value.push({ uri });

                    // Create a URI for the preview image and add it to operasional.documents
                    
                    props.operasional.documents = [
                        ...(props.operasional.documents || []),
                        { uri, id }, // If photo ID is returned from the server
                    ];

                } else {
                    alert(msg || "Data tidak disimpan.");
                }
            } catch (error) {
                alert("Error: Something went wrong during the upload.");
                console.log(error)
            }
        };

        const handleImageClick = (uri) => {
            previewImage.value = uri;
        };

        return {
            selectedImages,
            previewImage,
            formatNumber,
            formatCurrency,
            formatQuantity,
            handleSelectImage,
            uploadImage,
            handleImageClick,
            onClose,
        };
    },
};
</script>

<style scoped>
.container {
    padding: 20px;
}

.preview-thumbnail {
    cursor: pointer;
    transition: transform 0.2s;
}

.preview-thumbnail:hover {
    transform: scale(1.1);
}
</style>