<template>
    <div>
        <div class="header-container">
            <h2 class="title">Daftar Operasional</h2>
            <i class="material-icons add-button" @click="handleAddButtonClick">add_circle</i>
        </div>

        <div v-if="loading" class="loading-container">
            <div class="spinner"></div>
        </div>

        <div v-else class="container-box">
            <template v-if="operasionalData.length > 0">
                <OperasionalItem v-for="(item, index) in operasionalData" :key="index" :operasional="item"
                :onPress="handleItemClick"/>
            </template>
            <p v-else class="no-data-text">Tidak ada Data Operasional</p>
        </div>

        <v-dialog v-model="showDetails" max-width="600">
            <OperasionalShowScreen v-if="selectedItem && selectedItem.status === 'approved'" :operasional="selectedItem"
                @close="closeDetails"  @photo-uploaded="updateData" />
        </v-dialog>

        <v-dialog v-model="showEditForm" max-width="800">
            <OperasionalEditForm v-if="selectedItem && selectedItem.status === 'pending'" :selectedOperasional="selectedItem"
                @close="closeDetails" @submit="updateData" :paramsFromServer="paramsFromServer" :onSubmitCompleted="onSubmitCompleted"/>
        </v-dialog>


    </div>
</template>

<script>
import axios from "@/utils/axios";
import OperasionalItem from "./OperasionalItem";
import OperasionalShowScreen from "./OperasionalShowScreen.vue";
import OperasionalEditForm from "./OperasionalEditForm.vue";

export default {
    components: {
        OperasionalItem,
        OperasionalShowScreen,
        OperasionalEditForm,
    },
    data() {
        return {
            operasionalData: [],
            loading: true,
            formVisible: false,
            paramsFromServer: {},
            showDetails: false,
            showEditForm: false,
            selectedItem: null,
        };
    },
    methods: {
        async fetchOperasional() {
            try {
                const response = await axios.get("/operasional");
                this.operasionalData = response.data;
            } catch (error) {
                console.error(error);
            } finally {
                this.loading = false;
            }
        },
        async fetchFormOperasional() {
            try {
                const response = await axios.get("/operasional/getOpsFormData");
                this.paramsFromServer = response.data;

            } catch (error) {
                console.error("Error fetching form operasional", error);
            }
        },
        async handleAddButtonClick() {
            if (!Object.keys(this.paramsFromServer).length) {
                this.loading = true;
                await this.fetchFormOperasional().then(() => {
                    this.loading = false;
                });
            }

            this.$router.push({
                name: 'OperasionalForm',
                params: { paramsFromServer: this.paramsFromServer },
            });
        },
        handleItemClick(item) {
            
            this.selectedItem = item;
            if (item.status === "approved") {
                this.showDetails = true;
            } else if (item.status === "pending") {
                this.showEditForm = true;
            }
        },
        closeDetails() {
            this.showDetails = false;
            this.showEditForm = false;
            this.selectedItem = null;
        },
        updateData(updatedItem) {
            const index = this.operasionalData.findIndex(
                (item) => item.id === updatedItem.id
            );
            if (index !== -1) {
                this.operasionalData.splice(index, 1, updatedItem);
            }
            this.closeDetails();
        },

        setLoading(value) {
            this.loading = value;
        },
        onSubmitCompleted(response) {
            if (response.success) {
                this.fetchOperasional();
                this.$toast.success(response.msg || "Data berhasil disimpan.");
            } else {
                this.$toast.error(response.msg || "Terjadi kesalahan. Silakan coba lagi.");
            }
            this.closeDetails();
        },
    },
    created() {
        this.fetchOperasional();
    },
};
</script>

<style scoped>
.header-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
}

.title {
    margin-left: 8px;
    padding: 10px;
    border-bottom: 1px solid black;
}

.container-box {
    padding: 10px;
}

.container {
    padding: 10px;
    background-color: #f5f5f5;
}

.loading-container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.spinner {
    width: 40px;
    height: 40px;
    border: 4px solid rgba(0, 0, 255, 0.3);
    border-radius: 50%;
    border-top-color: #0000ff;
    animation: spin 1s linear infinite;
}

.no-data-text {
    text-align: center;
    color: #666;
}

.add-button {
    font-size: 24px;
    color: blue;
    cursor: pointer;
}

@keyframes spin {
    to {
        transform: rotate(360deg);
    }
}
</style>